import React from 'react';
import ReplayIcon from "@mui/icons-material/Replay";
import StarRateIcon from "@mui/icons-material/StarRate";
import CloseIcon from "@mui/icons-material/Close";
import FavoriteIcon from "@mui/icons-material/Favorite";
import FlashOnIcon from "@mui/icons-material/FlashOn";
import { IconButton } from '@mui/material';


const Swipe = () => {

    function refreshCard(e) {
        e.preventDefault();
        window.location.reload(false);
    }
    return (
        <div className='fixed bottom-10 flex justify-evenly w-full'>
            <IconButton className='inButton' onClick={refreshCard}>
                < ReplayIcon className='text-yellow-400 p-0.5' fontSize="large" />
            </IconButton>

            <IconButton className='inButton'>
                < CloseIcon className='text-red-400 p-0.5' fontSize="large" />
            </IconButton>

            <IconButton className='inButton'>
                < FavoriteIcon className='text-green-400 p-0.5' fontSize="large" />
            </IconButton>

            <IconButton className='inButton'>
                < StarRateIcon className='text-blue-400 p-0.5' fontSize="large" />
            </IconButton>

            <IconButton className='inButton'>
                < FlashOnIcon className='text-violet-700 p-0.5' fontSize="large" />
            </IconButton>
        </div>
    );
};

export default Swipe;