import React, { useState, useEffect } from 'react';
import TinderCard from 'react-tinder-card';
import axios from '../axios';

const TinderCards = () => {

    const [people, setPeople] = useState([]);

    useEffect(() => {
        async function fetchData() {
            const req = await axios.get("/api/v1/cards");
            setPeople(req.data);
        }
        fetchData();
    }, []);

    const swiped = (direction, nameToDelete) => {
        // setLastDirection(direction)
    };

    const outOfFrame = (name) => {
    };





    return (
        <div className='w-full h-full '>
            <div className='flex justify-center mt-[10vh]'>
                {people.map((person) => (
                    <TinderCard
                        className="absolute"
                        key={person.name}
                        preventSwipe={["up", "down"]}
                        onSwipe={(dir) => swiped(dir, person.name)}
                        onCardLeftScreen={() => outOfFrame(person.name)}
                    >
                        <div className='w-auto'>
                            <img className='relative bg-white w-[600px]  lg:max-w-[85vh] h-[60vh] border rounded-3xl bg-cover bg-center' src={person.imgUrl} alt="" />
                            <h3 className='absolute bottom-3 mx-6 text-white font-semibold text-2xl'>{person.name}</h3>
                        </div>
                    </TinderCard>
                ))}

            </div>


        </div>
    );
};

export default TinderCards;